.item-container {
  display: flex;
  flex-wrap: nowrap;
  margin-bottom: 10px;

  .dropdown {
    width: 70%;
  }

  .dropdown-toggle, .dropdown-menu {
    width: 100%;
  }

  .quantity-value {
    margin-left: 10px;
    max-width: 10%;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}
